.member-container{
    padding: 20px 0;
    position: relative;
}
.member-left{
    float: left;
    width: 220px;
    padding-bottom: 40px;
    background-color: #F1EADF;
    .user-info{
        padding:30px 30px 15px;
        .avatar{
            width: 100px;
            height: 100px;
            margin: 0 auto;
            img{
                width: 100%;
                height: 100%;
                border-radius: 50%
            }
        }
        .user-name{
            text-align: center;
            font-size: 16px;
            line-height: 50px;
            color: #ff465a;
        }
        .icon-group-v{
            text-align: center;
            a{
                display: inline-block;
                width: 25px;
                height: 25px;
                border-radius: 12px;
                text-align: center;
                background-color: #FEC7CC;
                margin: 0 3px;
                &.active{
                    background-color: #FB3D51;
                }
                .icon{
                    width: 100%;
                    height: 100%;
                    background-repeat: no-repeat;
                    background-position: center center;
                }
                .icon-man-v{
                    background-image: url(../img/icon-man-v.png);
                }
                .icon-phone-v{
                    background-image: url(../img/icon-phone-v.png);
                }
                .icon-card-v{
                    background-image: url(../img/icon-card-v.png);
                }
                .icon-lock-v{
                    background-image: url(../img/icon-lock-v.png);
                }
            }
        }
    }
    .member-nav{
        padding-left: 20px;
        li{
            padding-left: 50px;
            a{
                display: block;
                position: relative;
                font-size: 16px;
                line-height: 60px;
                height: 60px;
                color: #333333;
                padding-left: 36px;
                .icon{
                    position: absolute;
                    width: 30px;
                    height: 30px;
                    background-repeat: no-repeat;
                    background-position: center center;
                    transform: translateY(-50%);
                    top: 50%;
                    left: 0;
                }
                &:hover{
                    color: #ff465a;
                }
                
                .icon-home{
                    background-image: url(../img/icon-home.png);
                }
                
                .icon-wallet{
                    background-image: url(../img/icon-wallet.png);
                }
                .icon-trend{
                    background-image: url(../img/icon-trend.png);
                }
                .icon-stock{
                    background-image: url(../img/icon-stock.png);
                }
                .icon-note{
                    background-image: url(../img/icon-note.png);
                }
                .icon-card{
                    background-image: url(../img/icon-card.png);
                }
                .icon-profile{
                    background-image: url(../img/icon-profile.png);
                }
                .icon-invite{
                    background-image: url(../img/icon-invite.png);
                }
                .icon-ring{
                    background-image: url(../img/icon-ring.png);
                }
            }
            &.active{
                background-color: #FFFFFF;
                a{
                    color: #ff465a;
                }
            }
        }
    }
}
.member-main{
    overflow: hidden;
    padding:0 30px;
    background-color: #fff;
    min-height: 805px; 
    .member-main-title {
        border-bottom: 1px solid #F4F4F4;
        margin-bottom: 20px;
        h3 {
            font-size: 18px;
            float: left;
            padding: 0 20px;
            border-bottom: 2px solid #BB9767;
            line-height: 60px;
            margin: 0;
            margin-bottom: -1px;
        }
        h4{
            a{
                display: block;
                text-align: center;
                color: #909090;
                font-size: 18px;
            }
            float: left;
            padding: 0 20px;
            border-bottom: 2px solid transparent;
            line-height: 60px;
            margin: 0;
            margin-bottom: -1px;
        }
        .fr {
            float: right;
            line-height: 60px;
        }
    }
}
.list-filter{
    margin-bottom: 20px;
    padding: 0 20px;
    ul > li{
        line-height: 25px;
        padding:5px 0;
        .title{
            padding: 5px 0;
        }
        a{
            color: #5d5d5d;
            display: inline-block;
            width: 75px;
            text-align: center;
            margin: 5px 0;
            &.active{
                background-color: #FB3D51;
                color: #ffffff;
                border-radius: 4px;
                &:hover{
                    color: #fff;
                }
            }
            &:hover{
                color: #fb3d51;
            }
        }
    }

    .title{
        float: left;
        margin-right: 20px;
    }
    .con{
        overflow: hidden;
        .fr{
            float: right;
        }
    }
}

.financing-list {
    padding: 0 20px;
    .item{
        border: 1px solid #D9D9D9;
        margin-bottom: 20px;
        .item-hd{
            background-color: #F4F4F4;
            line-height: 38px;
            padding: 0 20px;
            border-bottom: 1px solid #D9D9D9;
            .order-sn{
                color: #bb9767;
                padding: 0 3px;
            }
            .fr{
                float: right;
                color: #5d5d5d;
            }
            .tag{
                display: inline-block;
                width: 56px;
                height: 22px;
                padding-right: 3px;
                font-size: 14px;
                line-height: 22px;
                text-align: right;
                background: url(../img/icon-tag.png) no-repeat;
                color: #fff;
            }
        }
        .item-bd{
            padding: 0 20px;
            .unit{
                float: left;
                width: (100% /8);
                text-align: center;
                line-height: 62px;
                .title{
                    border-bottom: 1px solid #D9D9D9;
                }
            }
        }
        .item-ft{
            background-color: #F4F4F4;
            line-height: 38px;
            padding: 0 20px;
            border-top: 1px solid #D9D9D9;
            .btn-group{
                float: right;
                a{
                    display: inline-block;
                    margin: 0 5px;
                    background-color: #fff;
                    width: 80px;
                    text-align: center;
                    font-size: 12px;
                    line-height: 25px;
                    color: #5d5d5d;
                    border-radius: 4px;
                    &.red{
                        background-color: #FB3D51;
                        color: #ffffff;
                    }
                }

            }
        }
    }
}

.date-picker{
    input{
        width: 120px;
        padding: 0 3px;
        border-radius: 2px;
        border: 1px solid #D9D9D9;
        text-align: center;
    }
}

form.form{
    padding-bottom: 40px;
    .control-group{
        position: relative;
        margin: 20px 0;
        &::before,&::after{
            content: " ";
            display: table;
        }
        &::after{
            clear: both;
        }
        label{
            float: left;
            width: 103px;
            text-align: center;
            font-weight: 400;
            color: #5d5d5d;
            line-height: 25px;
            padding:10px 0;
            height: 45px;
        }
        .con{
            float: left;
            margin-left: 10px;
            .fake-input{
                border: 1px solid #D9D9D9;
                display: inline-block;
                width: 360px;
                height: 42px;
                input{
                    width: 330px;
                    padding:9px 10px;
                    border: none;
                    line-height: 22px;
                    &:focus{
                        outline: none;
                    }
                }
                &.vcode-input{
                    width: 200px;
                    input{
                        width: 180px;
                    }
                }
            }
            input{
                border: 1px solid #D9D9D9;                    
                padding:9px 10px;
                line-height: 22px;
                &.bank-location{
                    position: relative;
                    top: -1px;
                }
            }
            select{
                display: inline-block;
                border: 1px solid #D9D9D9;
                line-height: 42px;
                height: 42px;
                padding: 0 10px;
            }
            
            .text{
                padding:10px 0;
                line-height: 28px;
            }
            .tel-number{
                display: inline-block;
                margin-right: 20px;
            }
            .btn-vcode{
                font-size: 12px;
            }
        }
        .btn-submit{
            font-size: 16px;
            line-height: 30px;
            width: 160px;
            margin-top: 10px;
        }
    }
}

.record-table{
    width: 100%;
    thead{
        th{
           text-align: center;
           font-weight: 400;
           background-color: #F4F4F4; 
           line-height: 40px;
           border-top: 1px solid #D9D9D9;
           border-bottom: 1px solid #D9D9D9;
        }
    }
    tbody{
        tr:hover{
            td{
                color: #333333;
            }
        }
        td{
            padding:15px 8px;
            line-height: 25px;
            border-bottom: 1px solid #D9D9D9;
            color: #999999;
        }
    }
}
